body {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    background-color: #e63946;
    display: flex;
    justify-content: center;
    height: 100vh;
    margin: 0;
    margin-top: 20px;
}

.App {
    background: #f1faee;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0);
    width: 90%;
    max-width: 100%;
    margin-top: 50px;
    width:  100%;
}

h1 {
    font-size: 25px;
    margin-bottom: 20px;
    color: #1d3557;
    text-align: center;
}

form {
    display: flex;
    margin-bottom: 20px;
}

input[type="text"] {
    flex: 1;
    padding: 10px;
    font-size: 22px;
    border: 1px solid #a8dadc;
    border-radius: 4px 0 0 4px;
    outline: none;
}

button {
    padding: 10px;
    font-size: 18px;
    border: none;
    background: #1d3557;
    color: #fff;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
}

ul {
    list-style: none;
    padding: 3px;
    margin: 0;
}

li {
    background: #fff;
    margin-bottom: 10px;
    padding: 10px;
    font-size: 22px;
    border: 1px solid #ddd;
    border-radius: 4px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

  li span {
    flex: 1;
}

li input[type="text"] {
    flex: 1;
    padding: 5px;
    font-size: 19px;
    border: 1px solid #ddd;
    border-radius: 4px;
    outline: none;
}
  
  li button {
    margin-left: 5px;
    padding: 5px 10px;
}

.delete {
    background: #c1121f;
}

/* Media Queries */

/* Mobile devices (max-width: 480px) */
@media (max-width: 480px) {
    body {
        flex-direction: column;
        height: auto;
    }

    .App {
        width: 100%;
        padding: 15px;
        margin-top: 20px;
    }

    h1 {
        font-size: 22px;
    }

    input[type="text"] {
        font-size: 16px;
        padding: 8px;
    }

    button {
        font-size: 14px;
        padding: 8px;
    }

    li {
        font-size: 18px;
        padding: 8px;
    }
}

/* Small Tablets (max-width: 600px) */
@media (max-width: 600px) {
    .App {
        width: 90%;
        padding: 15px;
    }

    h1 {
        font-size: 24px;
    }

    input[type="text"] {
        font-size: 18px;
        padding: 8px;
    }

    button {
        font-size: 16px;
        padding: 10px;
    }

    li {
        font-size: 20px;
        padding: 10px;
    }
}

/* Tablets and small laptops (max-width: 768px) */
@media (max-width: 768px) {
    .App {
        width: 90%;
        padding: 18px;
    }

    h1 {
        font-size: 26px;
    }

    input[type="text"] {
        font-size: 20px;
    }

    button {
        font-size: 17px;
        padding: 10px;
    }

    li {
        font-size: 21px;
    }
}